import * as R from "ramda";
import { v4 as uuidv4 } from "uuid";

// isCategoryNotHaveSubcategory :: Category -> Boolean
const isCategoryNotHaveSubcategory = R.compose(
  R.isNil,
  R.prop("subcategories")
);

// prepareCategory :: Function -> CategoryFormData -> SubmitCategory
const prepareCategory = (prepareSubcategory) =>
  R.ifElse(
    isCategoryNotHaveSubcategory,
    R.dissoc("subcategories"),
    R.converge(R.mergeRight, [
      R.identity,
      R.compose(
        R.objOf("subcategories"),
        R.map(prepareSubcategory),
        R.prop("subcategories")
      ),
    ])
  );

// populateSubcategoryWithId :: SubcategoryFormData -> SubcategoryInput
const populateSubcategoryWithId = R.applySpec({
  _id: () => uuidv4(),
  name: R.prop("name"),
  markup: R.prop("markup"),
});

// populateSubcategoryWithIdIfNeed :: SubcategoryFormData -> SubcategoryUpdate
const populateSubcategoryWithIdIfNeed = R.ifElse(
  R.compose(R.isNil, R.prop("_id")),
  populateSubcategoryWithId,
  R.identity
);

// prepareCreateCategoryBeforSubmit :: FormDataInput -> CategoryInput
export const prepareCreateCategoryBeforSubmit = prepareCategory(
  populateSubcategoryWithId
);

// prepareUpdateCategoryBeforSubmit :: FormDataInput -> CategoryUpdate
export const prepareUpdateCategoryBeforSubmit = prepareCategory(
  populateSubcategoryWithIdIfNeed
);

// prepareCategoryIntialValues :: CategotyData -> CategoryIntialValues
export const prepareCategoryIntialValues = R.compose(
  R.unless(
    isCategoryNotHaveSubcategory,
    R.converge(R.mergeRight, [
      R.identity,
      R.compose(
        R.objOf("subcategories"),
        R.map(R.dissoc("__typename")),
        R.prop("subcategories")
      ),
    ])
  ),
  R.dissoc("__typename"),
  R.propOr({}, "category")
);
