import * as R from "ramda";
import { useQuery, gql } from "@apollo/client";
import { Table, Button } from "antd";

import { EditCategoryForm } from "../CategoryForm/EditCategoryForm";
import { ButtonWrapper, EditButton } from "../../../components/Button";
import { AddCategoryForm } from "../CategoryForm/AddCategoryForm";
import { useModalContext } from "../../../providers/ModalProvider";
import { DeleteCategoryBtn } from "./DeleteCategoryBtn";
import { categoryFragment } from "../../price/PriceTable/PriceTable";

export const CATEGORIES_QUERY = gql`
  query {
    categories {
      ...categoryFragment
    }
  }
  ${categoryFragment}
`;

// isSubcategory :: Object -> Boolean
const isSubcategory = R.propEq("__typename", "Subcategory");

// prepareCategoryData :: QueryData -> [Category]
const prepareCategoryData = R.compose(
  R.map(
    R.unless(
      R.compose(R.either(R.isNil, R.isEmpty), R.prop("subcategories")),
      R.converge(R.mergeRight, [
        R.dissoc("subcategories"),
        R.compose(R.objOf("children"), R.prop("subcategories")),
      ])
    )
  ),
  R.propOr([], "categories")
);

export const CategoryTable = () => {
  const { data, loading, refetch } = useQuery(CATEGORIES_QUERY, {
    fetchPolicy: "network-only",
  });

  const { showModal, handleCancel } = useModalContext();

  const categories = prepareCategoryData(data);

  const showAddCategoryModal = () =>
    showModal({
      title: "  ",
      content: () => (
        <AddCategoryForm closeModal={handleCancel} refetch={refetch} />
      ),
    });

  const showEditCategoryModal = (id, categoryName) =>
    showModal({
      title: `  ${categoryName}`,
      content: () => (
        <EditCategoryForm closeModal={handleCancel} refetch={refetch} id={id} />
      ),
    });

  const columnsConfig = [
    {
      title: "",
      dataIndex: "name",
    },
    {
      title: "",
      dataIndex: "markup",
    },
    {
      dataIndex: "_id",
      render: (id, category) =>
        isSubcategory(category) ? null : (
          <ButtonWrapper>
            <EditButton
              onClick={() => showEditCategoryModal(id, category.name)}
            />
            <DeleteCategoryBtn
              refetch={refetch}
              id={id}
              categoryName={category.name}
            />
          </ButtonWrapper>
        ),
    },
  ];

  return (
    <>
      <ButtonWrapper>
        <Button type="primary" onClick={showAddCategoryModal}>
           
        </Button>
      </ButtonWrapper>

      <Table
        columns={columnsConfig}
        dataSource={categories}
        loading={loading}
        rowKey="_id"
        pagination={false}
        size="small"
      />
    </>
  );
};
