import { useMutation, gql } from "@apollo/client";
import { BatchOrderForm } from "./BatchOrderForm";

const ADD_BATCH_ORDERS = gql`
  mutation createBatchOrders($input: BatchOrdersInput!) {
    createBatchOrders(input: $input) {
      _id
    }
  }
`;

export const AddBatchOrdersForm = ({ closeModal, refetch }) => {
  const [createBatchOrders] = useMutation(ADD_BATCH_ORDERS);

  const handleSubmit = async (values) => {
    try {
      await createBatchOrders({
        variables: {
          input: values,
        },
      });
      closeModal();
      refetch();
    } catch (e) {
      alert(e);
    }
  };

  return <BatchOrderForm onFinish={handleSubmit} />;
};
