import { gql, useMutation } from "@apollo/client";
import { Form, InputNumber } from "antd";
import { requiredRule, SubmitBtnItem } from "./Form";

const SettingsForm = ({ onFinish, initialValues }) => {
  return (
    <Form
      name="settingsForm"
      onFinish={onFinish}
      layout={"vertical"}
      initialValues={initialValues}
    >
      <Form.Item
        label=" "
        name="dropshippingMarkup"
        rules={[requiredRule]}
      >
        <InputNumber />
      </Form.Item>

      <SubmitBtnItem />
    </Form>
  );
};

const UPDATE_SETTINGS = gql`
  mutation updateSettings($update: SettingsInput!) {
    updateSettings(update: $update) {
      dropshippingMarkup
    }
  }
`;

export const UpdateSettingsForm = ({ closeModal }) => {
  const [updateSettings] = useMutation(UPDATE_SETTINGS);

  const initialValues = {
    dropshippingMarkup: localStorage.getItem("setting"),
  };

  const handleSubmit = async (values) => {
    try {
      await updateSettings({
        variables: {
          update: values,
        },
      });
      localStorage.setItem("setting", values.dropshippingMarkup);
      closeModal();
      window.location.reload();
    } catch (e) {
      alert(e);
    }
  };

  return <SettingsForm onFinish={handleSubmit} initialValues={initialValues} />;
};
