import { useMutation, gql } from "@apollo/client";

import { DeleteButton } from "../../../components/Button";
import { useConfirmDeleteModal } from "../../../hook/useCinfirmModal";

const DELETE_ORDER_MUTATION = gql`
  mutation removeOrder($batchOrderId: ID!, $orderId: ID!) {
    removeOrder(batchOrderId: $batchOrderId, orderId: $orderId) {
      _id
    }
  }
`;

export const DeleteOrderBtn = ({ batchOrderId, orderId, refetch }) => {
  const [removeOrder] = useMutation(DELETE_ORDER_MUTATION, {
    variables: { batchOrderId, orderId },
  });

  const showDeleteConfirmModal = useConfirmDeleteModal(
    "",
    removeOrder,
    refetch
  );

  return <DeleteButton onClick={showDeleteConfirmModal} />;
};
