import { useMutation, gql } from "@apollo/client";
import * as R from "ramda";
import moment from "moment";

import { prepareValuesBeforeSubmit } from "./AddOwnOrderForm";
import { OwnOrderForm } from "./OwnOrderForm";

const UPDATE__OWN_ORDER = gql`
  mutation updateOwnOrder($id: ID!, $update: OwnOrderInput!) {
    updateOwnOrder(id: $id, update: $update) {
      _id
    }
  }
`;

const getProductIds = R.compose(R.map(R.prop("_id")), R.propOr([], "products"));

export const UpdateOwnOrdersForm = ({ closeModal, refetch, order }) => {
  const [updateOwnOrder] = useMutation(UPDATE__OWN_ORDER);

  const productIds = getProductIds(order);

  const initialValues = { ...order, productIds, date: moment(order.date) };

  const initialProducts = R.propOr([], "products", order);

  const handleSubmit = async (values) => {
    try {
      updateOwnOrder({
        variables: {
          id: order._id,
          update: { ...prepareValuesBeforeSubmit(values) },
        },
      });
      closeModal();
      refetch();
    } catch (e) {
      alert(e);
    }
  };

  return (
    <OwnOrderForm
      onFinish={handleSubmit}
      initialValues={initialValues}
      initialProducts={initialProducts}
    />
  );
};
