import { DeleteDropBuyerBtn } from "./DeleteDropBuyerBtn";
import { useQuery, gql } from "@apollo/client";
import { Table, Button } from "antd";
import * as R from "ramda";
import { generatePath } from "react-router";

import { ButtonWrapper, EditButton } from "../../components/Button";
import { useModalContext } from "../../providers/ModalProvider";
import { AddDropBuyerForm } from "./dropBuyerForm/AddDropBuyerForm";
import { EditDropBuyerForm } from "./dropBuyerForm/EditDropBuyerForm";
import styled from "styled-components";
import { routes } from "../../routes/config";
import { Link } from "react-router-dom";

const AddDropBuyerBtn = styled(Button)`
  margin: 10px;
`;

export const DROPBUYERS_QUERY = gql`
  query {
    dropBuyers {
      _id
      name
      isActive
    }
  }
`;

export const DropBuyerTable = () => {
  const { data, loading, refetch } = useQuery(DROPBUYERS_QUERY, {
    fetchPolicy: "network-only",
  });

  const { showModal, handleCancel } = useModalContext();

  const showAddDropBuyerModal = () =>
    showModal({
      title: "  ",
      content: () => (
        <AddDropBuyerForm closeModal={handleCancel} refetch={refetch} />
      ),
    });

  const showEditDropBuyerModal = (id, name) =>
    showModal({
      title: `  ${name}`,
      content: () => (
        <EditDropBuyerForm
          closeModal={handleCancel}
          refetch={refetch}
          id={id}
        />
      ),
    });

  const columnsConfig = [
    {
      title: "",
      dataIndex: "name",
      render: (name, dropBuyer) => {
        const path = generatePath(routes.batchOrdersBuyer, {
          dropBuyerId: dropBuyer._id,
        });
        return <Link to={path}>{name}</Link>;
      },
    },

    {
      title: "",
      dataIndex: "isActive",
      render: (isActive) => (isActive ? "" : ""),
    },

    {
      dataIndex: "_id",
      render: (id, dropBuyer) => (
        <ButtonWrapper>
          <EditButton
            onClick={() => showEditDropBuyerModal(id, dropBuyer.name)}
          />
          <DeleteDropBuyerBtn refetch={refetch} id={id} name={dropBuyer.name} />
        </ButtonWrapper>
      ),
    },
  ];

  return (
    <>
      <AddDropBuyerBtn type="primary" onClick={showAddDropBuyerModal}>
         
      </AddDropBuyerBtn>

      <Table
        columns={columnsConfig}
        dataSource={R.propOr([], "dropBuyers", data)}
        loading={loading}
        rowKey="_id"
        pagination={false}
        size="small"
      />
    </>
  );
};
