import { Form, Input, InputNumber, Space, Select, DatePicker } from "antd";
import * as R from "ramda";

import { requiredRule, SubmitBtnItem } from "../../../components/Form";
import { useUserContext } from "../../../providers/UserProvider";
import { ORDER_STATUSES } from "../../batchOrder/form/OrderForm";
import { ProductItems } from "./ProductItems";

export const OWN_ORDER_STATUSES = {
  ...ORDER_STATUSES,
  RETURNED: "",
};

export const OwnOrderForm = ({
  onFinish,
  initialValues,
  initialProducts = [],
}) => {
  const [form] = Form.useForm();
  const { user } = useUserContext();
  const isAdmin = user.role === "ADMIN";

  return (
    <Form
      form={form}
      name="orderForm"
      onFinish={onFinish}
      layout={"vertical"}
      initialValues={initialValues}
    >
      <Form.Item name="date" label="" rules={[requiredRule]}>
        <DatePicker />
      </Form.Item>
      <Form.Item label="" name="ttn">
        <Input />
      </Form.Item>
      <Form.Item
        label=""
        name="status"
        initialValue={OWN_ORDER_STATUSES.PAID_BEFORE}
      >
        <Select>
          {R.values(OWN_ORDER_STATUSES).map((status) => (
            <Select.Option key={status} value={status}>
              {status}
            </Select.Option>
          ))}
        </Select>
      </Form.Item>
      <Form.Item label="" name="buyerInfo">
        <Input />
      </Form.Item>
      <ProductItems form={form} initialProducts={initialProducts} />
      <Form.Item name="payed" label="">
        <InputNumber min={0} />
      </Form.Item>

      <Space>
        <Form.Item
          name="totalPrice"
          label="  "
          rules={[requiredRule]}
          initialValue={0}
        >
          <InputNumber min={0} disabled={!isAdmin} />
        </Form.Item>
        <Form.Item
          name="totalSellPrice"
          label=" "
          rules={[requiredRule]}
          initialValue={0}
        >
          <InputNumber min={0} disabled={!isAdmin} />
        </Form.Item>
      </Space>

      <SubmitBtnItem />
    </Form>
  );
};
