import { useMemo, useRef, useState } from "react";
import XLSX from "xlsx";
import * as R from "ramda";
import { Button } from "antd";
import styled from "styled-components";

import { UpdatePrice } from "./UpdatePrice";

const HiddenInput = styled.input`
  opacity: 0;
  width: 0;
  height: 0;
`;

const preparePrice = R.map(
  R.applySpec({
    nomenclature: R.propOr("", ""),
    article: R.propOr("", ""),
    count: R.compose(parseInt, R.propOr(0, "")),
    price: R.compose(parseFloat, R.propOr(0, "")),
  })
);

const prepareOldPrice = R.map(
  R.pick(["nomenclature", "article", "count", "price"])
);

const filterNewItemsDiff = (nomenclatures) =>
  R.filter(R.compose(R.includes(R.__, nomenclatures), R.prop("article")));

const filterNewItemsWithOutDiff = (nomenclatures) =>
  R.filter(
    R.compose(R.not, R.includes(R.__, nomenclatures), R.prop("article"))
  );

const getNomenclatureItemsDiff = (oldPrice, newPrice) =>
  R.ifElse(
    R.isEmpty,
    R.always([]),
    R.converge(R.difference, [
      R.pluck("article"),
      R.always(R.pluck("article", oldPrice)),
    ])
  )(newPrice);
export const UpdatePriceExcel = ({ oldProductPrice, closeModal, refetch }) => {
  const [newPrice, setNewPrice] = useState([]);

  const ref = useRef();

  const oldPrice = useMemo(
    () => prepareOldPrice(oldProductPrice),
    [oldProductPrice]
  );

  const nomencaltureDiff = useMemo(
    () => getNomenclatureItemsDiff(oldPrice, newPrice),
    [newPrice, oldPrice]
  );

  const diffProducts = useMemo(
    () => filterNewItemsDiff(nomencaltureDiff)(newPrice),
    [newPrice, nomencaltureDiff]
  );

  const updateProductsItems = useMemo(
    () => filterNewItemsWithOutDiff(nomencaltureDiff)(newPrice),
    [newPrice, nomencaltureDiff]
  );

  const tiggerInput = () => ref.current.click();

  const handleChange = (e) => {
    const files = e.target.files;

    if (files && files[0]) {
      const reader = new FileReader();
      const rABS = !!reader.readAsBinaryString;

      reader.onload = (e) => {
        const bstr = e.target.result;
        const wb = XLSX.read(bstr, {
          type: rABS ? "binary" : "array",
          bookVBA: true,
        });

        const wsname = wb.SheetNames[0];
        const ws = wb.Sheets[wsname];
        const data = XLSX.utils.sheet_to_json(ws);
        const preparedData = preparePrice(data);
        setNewPrice(preparedData);
      };

      if (rABS) {
        reader.readAsBinaryString(files[0]);
      } else {
        reader.readAsArrayBuffer(files[0]);
      }
    }
  };

  return (
    <>
      <HiddenInput ref={ref} type="file" onChange={handleChange} />
      <Button onClick={tiggerInput}> </Button>
      <UpdatePrice
        updateProductsItems={updateProductsItems}
        diffProducts={diffProducts}
        setNewPrice={setNewPrice}
        closeModal={closeModal}
        refetch={refetch}
      />
    </>
  );
};
