import { Button } from "antd";
import ReactExport from "react-export-excel";

const ExcelFile = ReactExport.ExcelFile;
const ExcelSheet = ReactExport.ExcelFile.ExcelSheet;
const ExcelColumn = ReactExport.ExcelFile.ExcelColumn;

export const ExcelExport = ({ price }) => {
  return (
    <ExcelFile element={<Button type="primary">Excel Export</Button>}>
      <ExcelSheet data={price} name="Price">
        <ExcelColumn label="" value="nomenclature" />
        <ExcelColumn label="" value="article" />
        <ExcelColumn label="" value="count" />
        <ExcelColumn label="" value="price" />
        <ExcelColumn label=" " value="dropPrice" />
        <ExcelColumn label="" value="sellingPrice" />
        <ExcelColumn label="" value="wholesalePrice" />
        <ExcelColumn label="" value="type" />
        <ExcelColumn label="" value="categoryName" />
        <ExcelColumn label=" " value="subcategoryName" />
      </ExcelSheet>
    </ExcelFile>
  );
};

