import { useMutation, gql } from "@apollo/client";

import { DeleteButton } from "../../../components/Button";
import { useConfirmDeleteModal } from "../../../hook/useCinfirmModal";

const DELETE_OWN_ORDER_MUTATION = gql`
  mutation removeOwnOrder($id: ID!) {
    removeOwnOrder(id: $id)
  }
`;

export const DeleteOwnOrderBtn = ({ id, refetch }) => {
  const [removeOwnOrder] = useMutation(DELETE_OWN_ORDER_MUTATION, {
    variables: { id },
  });

  const showDeleteConfirmModal = useConfirmDeleteModal(
    "",
    removeOwnOrder,
    refetch
  );

  return <DeleteButton onClick={showDeleteConfirmModal} />;
};
