import { useMutation, gql } from "@apollo/client";

import { DeleteButton } from "../../../components/Button";
import { useConfirmDeleteModal } from "../../../hook/useCinfirmModal";

const DELETE_CATEGORY_MUTATION = gql`
  mutation deleteCategory($categoryId: ID!) {
    deleteCategory(id: $categoryId)
  }
`;

export const DeleteCategoryBtn = ({ id, categoryName, refetch }) => {
  const [deleteCategory] = useMutation(DELETE_CATEGORY_MUTATION, {
    variables: { categoryId: id },
  });

  const showDeleteConfirmModal = useConfirmDeleteModal(
    categoryName,
    deleteCategory,
    refetch
  );

  return <DeleteButton onClick={showDeleteConfirmModal} />;
};
