import { useQuery, gql } from "@apollo/client";
import { Table, Button, DatePicker } from "antd";
import * as R from "ramda";
import moment from "moment";
import styled from "styled-components";
import { useModalContext } from "../../providers/ModalProvider";

import { UpdateOwnOrdersForm } from "./form/UpdateOwnOrdersForm";
import {
  ButtonWrapper,
  EditButton,
  OrderDetailBtn,
} from "../../components/Button";
import { DeleteOwnOrderBtn } from "./form/DeleteOwnOrderBtn";
import { ProductsOrderTable } from "../../components/ProductsOrderTable";
import { useState } from "react";
import { isInCalculateStatus } from "../batchOrder/BatchOrderTable";
import { AddOwnOrdersForm } from "./form/AddOwnOrderForm";
import { roundToKopeck } from "../price/PriceTable/priceTableUtils";

export const InfoWrapper = styled.div`
  display: flex;
  align-items: center;
  gap: 20px;
  margin-left: 10px;
`;

export const OWN_ORDERS_QUERY = gql`
  query ownOrders($date: String!) {
    ownOrders(date: $date) {
      _id
      date
      totalPrice
      totalSellPrice
      ttn
      buyerInfo
      status
      payed
      products {
        _id
        article
        count
        price
        sellingPrice
        nomenclature
      }
    }
  }
`;

const calculateOwnOrderProfit = R.compose(
  roundToKopeck,
  R.converge(R.subtract, [
    R.compose(R.sum, R.map(R.propOr(0, "totalSellPrice"))),
    R.compose(R.sum, R.map(R.propOr(0, "totalPrice"))),
  ]),
  R.filter(isInCalculateStatus)
);

const calculatePayed = R.compose(
  roundToKopeck,
  R.sum,
  R.map(R.propOr(0, "payed"))
);

const calculateTotal = R.compose(
  roundToKopeck,
  R.converge(R.subtract, [calculateOwnOrderProfit, calculatePayed])
);

export const OwnOrdersPage = () => {
  const currentMonth = new Date().toISOString().slice(0, 7);

  const [filterDate, setFilterDate] = useState(currentMonth);

  const onChangeDate = (_, dateString) => {
    setFilterDate(dateString);
  };

  const { showModal, handleCancel } = useModalContext();

  const { data, loading, refetch } = useQuery(OWN_ORDERS_QUERY, {
    variables: {
      date: filterDate,
    },
    skip: !filterDate,
    fetchPolicy: "network-only",
  });

  const orders = R.compose(R.pathOr([], ["ownOrders"]))(data);

  const profit = calculateTotal(orders);

  const showAddOrderModal = () =>
    showModal({
      title: `    `,
      content: () => (
        <AddOwnOrdersForm closeModal={handleCancel} refetch={refetch} />
      ),
    });

  const showEditOrderModal = (order) =>
    showModal({
      title: ` `,
      content: () => (
        <UpdateOwnOrdersForm
          closeModal={handleCancel}
          refetch={refetch}
          order={order}
        />
      ),
    });

  const showOrderDetailModal = (products) =>
    showModal({
      title: "",
      width: 1000,
      content: () => <ProductsOrderTable products={products} type="own" />,
    });

  const columnsConfig = [
    {
      title: "",
      dataIndex: "date",
      render: (value) => moment(value).format("YYYY-MM-DD"),
    },
    {
      title: "",
      dataIndex: "buyerInfo",
    },
    {
      title: "",
      dataIndex: "ttn",
    },
    {
      title: "  ",
      dataIndex: "totalPrice",
    },

    {
      title: " ",
      dataIndex: "totalSellPrice",
    },

    {
      title: "",
      dataIndex: "payed",
    },

    {
      title: "",
      dataIndex: "status",
    },

    {
      dataIndex: "_id",
      render: (_, order) => (
        <ButtonWrapper>
          <OrderDetailBtn
            onClick={() =>
              showOrderDetailModal(R.propOr([], "products", order))
            }
          />
          <EditButton onClick={() => showEditOrderModal(order)} />
          <DeleteOwnOrderBtn id={order._id} refetch={refetch} />
        </ButtonWrapper>
      ),
    },
  ];

  return (
    <>
      <InfoWrapper>
        <DatePicker
          onChange={onChangeDate}
          picker="month"
          defaultValue={moment(currentMonth, "YYYY/MM")}
        />
        <Button
          style={{ margin: "10px" }}
          type="primary"
          onClick={showAddOrderModal}
        >
           
        </Button>

        <p>: {profit} </p>
      </InfoWrapper>

      <Table
        columns={columnsConfig}
        dataSource={orders}
        loading={loading}
        rowKey="_id"
        pagination={false}
        size="small"
        scroll={{ y: 800 }}
      />
    </>
  );
};
