import { Select, Form } from "antd";
import { useQuery } from "@apollo/client";
import * as R from "ramda";

import { DROPBUYERS_QUERY } from "../../dropBuyer/DropBuyerTable";
import { requiredRule } from "../../../components/Form";

const { Option } = Select;

export const DropBuyerSelect = () => {
  const { data } = useQuery(DROPBUYERS_QUERY, {
    fetchPolicy: "network-only",
  });

  const options = R.propOr([], "dropBuyers", data);

  return (
    <Form.Item label="" name="dropBuyerId" rules={[requiredRule]}>
      <Select style={{ width: "100%" }}>
        {options.map((dropBuyer) => (
          <Option key={dropBuyer._id} value={dropBuyer._id}>
            {dropBuyer.name}
          </Option>
        ))}
      </Select>
    </Form.Item>
  );
};
