import { useMutation, gql } from "@apollo/client";

import { DeleteButton } from "../../components/Button";
import { useConfirmDeleteModal } from "../../hook/useCinfirmModal";

const REMOVE_BATCH_ORDERS_MUTATION = gql`
  mutation removeBatchOrders($id: ID!) {
    removeBatchOrders(id: $id)
  }
`;

export const DeleteBatchBtn = ({ id, batchName, refetch }) => {
  const [deleteBatch] = useMutation(REMOVE_BATCH_ORDERS_MUTATION, {
    variables: { id },
  });

  const showDeleteConfirmModal = useConfirmDeleteModal(
    batchName,
    deleteBatch,
    refetch
  );

  return <DeleteButton onClick={showDeleteConfirmModal} />;
};
