import { useQuery } from "@apollo/client";
import { Button, Table } from "antd";
import * as R from "ramda";
import { useState } from "react";
import { Select } from "antd";

import { generatePath, Link, useParams } from "react-router-dom";
import { routes } from "../routes/config";
import { BATCH_ORDERS_QUERY } from "./batchOrders/BatchOrdersTable";
import { Wrapper } from "./PublicBatchOrder";

const { Option } = Select;

export const PublicBatchOrders = () => {
  const { dropBuyerId } = useParams();
  const query = {
    isActive: true,
    dropBuyerId,
  };

  const [internalQuery, setInternalQuery] = useState(query);

  const { data, loading } = useQuery(BATCH_ORDERS_QUERY, {
    variables: {
      query: internalQuery,
    },
    skip: !internalQuery,
    fetchPolicy: "network-only",
  });

  const columnsConfig = [
    {
      title: "",
      dataIndex: "name",
      render: (name, batchOrder) => {
        const path = generatePath(
          `${routes.public}/${routes.publicBatchOrder}`,
          {
            batchOrderId: batchOrder._id,
          }
        );
        return <Link to={path}>{name}</Link>;
      },
    },
    {
      title: "",
      dataIndex: "dropBuyer",
      render: (dropBuyer) => dropBuyer.name,
    },
    {
      title: "",
      dataIndex: "isActive",
      render: (isActive) => (isActive ? "" : ""),
    },
  ];

  const handleChangeBatchFilter = (value) => {
    setInternalQuery({ ...internalQuery, isActive: value });
  };

  return (
    <Wrapper>
      <Select
        defaultValue={true}
        style={{ width: 120, margin: "10px" }}
        onChange={handleChangeBatchFilter}
      >
        <Option value={true}></Option>
        <Option value={false}> </Option>
      </Select>
      <Button href="/price" type="primary" target="_blank">
        
      </Button>
      <Table
        columns={columnsConfig}
        dataSource={R.compose(
          R.reverse,
          R.sortBy(R.prop("name")),
          R.propOr([], "batchOrders")
        )(data)}
        loading={loading}
        rowKey="_id"
        pagination={false}
        size="small"
      />
    </Wrapper>
  );
};
