import { useMutation, gql, useQuery } from "@apollo/client";
import * as R from "ramda";
import {
  prepareCategoryId,
  prepareProductBeforeSubmit,
} from "../ProductForm/utils";

import { ProductSetForm } from "./ProductSetForm";

const UPDATE_PRODUCT_SET_MUTATION = gql`
  mutation updateProductSet($id: ID!, $update: ProductSetInput!) {
    updateProductSet(id: $id, update: $update) {
      _id
    }
  }
`;

const PRODUCT_SET_QUERY = gql`
  query productSet($id: ID!) {
    productSet(id: $id) {
      _id
      nomenclature
      article
      products {
        _id
        nomenclature
        article
        count
        price
      }
      category {
        _id
        name
        subcategories {
          _id
          name
        }
      }
      subcategoryId
    }
  }
`;

const getInitialValues = R.compose(
  prepareCategoryId,
  R.converge(R.mergeRight, [
    R.identity,
    R.compose(R.objOf("productIds"), R.pluck("_id"), R.propOr([], "products")),
  ]),
  R.propOr([], "productSet")
);

export const EditProductSetForm = ({ id, closeModal, refetch }) => {
  const [updateProductSet] = useMutation(UPDATE_PRODUCT_SET_MUTATION);

  const { data, loading } = useQuery(PRODUCT_SET_QUERY, {
    variables: { id },
    fetchPolicy: "network-only",
  });

  if (loading) return null;

  const initialProducts = R.pathOr([], ["productSet", "products"], data);

  const initialValues = getInitialValues(data);

  const handleSubmit = async (values) => {
    await updateProductSet({
      variables: {
        id,
        update: prepareProductBeforeSubmit(values),
      },
    });
    closeModal();
    refetch();
  };

  return (
    <ProductSetForm
      onFinish={handleSubmit}
      initialValues={initialValues}
      initialProducts={initialProducts}
    />
  );
};
