import { useQuery } from "@apollo/client";
import { Form, Input, Select, InputNumber } from "antd";
import * as R from "ramda";
import { useState } from "react";
import styled from "styled-components";

import {
  ArticleInputItem,
  MarkupInputItem,
  requiredRule,
  SubmitBtnItem,
} from "../../../components/Form";
import { CATEGORIES_QUERY } from "../../category/CategoryTable/CategoryTable";

// getSubcategories :: Category -> Subcategory Null
const getSubcategories = R.curry((categoryId, subcategories) =>
  R.compose(
    R.propOr(null, "subcategories"),
    R.find(R.propEq("_id", categoryId))
  )(subcategories)
);

const FlexWrapper = styled.div`
  display: flex;
  justify-content: flex-start;
  gap: 30px;
`;

export const useCategoryId = (initialValues) => {
  const [categoryId, setCategoryId] = useState(initialValues.categoryId);

  const onValuesChange = (_, { categoryId }) => {
    if (categoryId) {
      setCategoryId(categoryId);
    }
  };

  return {
    categoryId,
    onValuesChange,
  };
};

export const CategorySelectItem = ({ categoryId }) => {
  const { data, loading } = useQuery(CATEGORIES_QUERY, {
    fetchPolicy: "network-only",
  });

  const categories = R.propOr([], "categories", data);

  if (loading) return null;

  const subcategories = getSubcategories(categoryId, categories);

  return (
    <>
      <Form.Item label="" name="categoryId" rules={[requiredRule]}>
        <Select
          showSearch
          filterOption={(input, option) =>
            option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
          }
        >
          {categories.map(({ name, _id }) => (
            <Select.Option key={_id} value={_id}>
              {name}
            </Select.Option>
          ))}
        </Select>
      </Form.Item>
      {subcategories && (
        <Form.Item label="" name="subcategoryId">
          <Select
            allowClear={true}
            showSearch
            filterOption={(input, option) =>
              option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
            }
          >
            {subcategories.map(({ name, _id }) => (
              <Select.Option key={_id} value={_id}>
                {name}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
      )}
    </>
  );
};

export const ProductForm = ({ onFinish, initialValues = {} }) => {
  const { categoryId, onValuesChange } = useCategoryId(initialValues);

  return (
    <Form
      onValuesChange={onValuesChange}
      name="productForm"
      onFinish={onFinish}
      layout={"vertical"}
      initialValues={initialValues}
    >
      <Form.Item
        label=""
        name="nomenclature"
        rules={[requiredRule]}
      >
        <Input />
      </Form.Item>
      <ArticleInputItem />
      <FlexWrapper>
        <Form.Item label="" name="count" rules={[requiredRule]}>
          <InputNumber min={0} />
        </Form.Item>
        <Form.Item label="" name="price" rules={[requiredRule]}>
          <InputNumber min={0} />
        </Form.Item>
        <MarkupInputItem />
      </FlexWrapper>

      <CategorySelectItem categoryId={categoryId} />
      <SubmitBtnItem />
    </Form>
  );
};
