import { useMutation, useQuery, gql } from "@apollo/client";

import { ProductForm } from "./ProductForm";
import {
  prepareProductBeforeSubmit,
  prepareProductInitialValues,
} from "./utils";

const UPDATE_PRODUCT_MUTATION = gql`
  mutation updateProduct($id: ID!, $update: ProductInput!) {
    updateProduct(id: $id, update: $update) {
      _id
    }
  }
`;

const PRODUCT_QUERY = gql`
  query product($id: ID!) {
    product(id: $id) {
      _id
      nomenclature
      article
      count
      price
      subcategoryId
      markup
      category {
        _id
        name
        markup
        subcategories {
          _id
          name
          markup
        }
      }
    }
  }
`;

export const EditProductForm = ({ id, closeModal, refetch }) => {
  const [updateProduct] = useMutation(UPDATE_PRODUCT_MUTATION);

  const { data, loading } = useQuery(PRODUCT_QUERY, {
    variables: { id },
    fetchPolicy: "network-only",
  });

  if (loading) return null;

  const initialValues = prepareProductInitialValues(data);

  const handleSubmit = async (values) => {
    try {
      await updateProduct({
        variables: {
          id,
          update: prepareProductBeforeSubmit(values),
        },
      });
      closeModal();
      refetch();
    } catch (e) {
      alert(e);
    }
  };

  return <ProductForm onFinish={handleSubmit} initialValues={initialValues} />;
};
