import { Button } from "antd";
import { ProductShortTable } from "../../../components/ProductShortTable";
import styled from "styled-components";
import { useMutation, gql } from "@apollo/client";
import * as R from "ramda";

const BtnWrapper = styled.div`
  padding: 15px 0;
  display: flex;
  gap: 10px;
`;

const UPDATE_PRODUCTS = gql`
  mutation updateProducts($update: [ProductsInput]!) {
    updateProducts(update: $update)
  }
`;

const CREATE_PRODUCTS = gql`
  mutation createProducts($input: [ProductsInput]!) {
    createProducts(input: $input)
  }
`;

export const UpdatePrice = ({
  updateProductsItems,
  diffProducts,
  setNewPrice,
  closeModal,
  refetch,
}) => {
  const [createProducts] = useMutation(CREATE_PRODUCTS);

  const [updateProducts, { loading }] = useMutation(UPDATE_PRODUCTS);

  const handleUpdateProducts = async () => {
    const res = await updateProducts({
      variables: {
        update: updateProductsItems,
      },
    });
    await refetch();
    const count = R.pathOr(0, ["data", "updateProducts"], res);
    alert(` ${count} `);
    setNewPrice([]);
    closeModal();
  };

  const handleCreateAndUpdate = async () => {
    const resCreate = await createProducts({
      variables: {
        input: diffProducts,
      },
    });

    let resUpdate;
    if (!R.isEmpty(updateProductsItems)) {
      resUpdate = await updateProducts({
        variables: {
          update: updateProductsItems,
        },
      });
    }

    await refetch();
    const countUpdate = R.pathOr(0, ["data", "updateProducts"], resUpdate);
    const countcareate = R.pathOr(0, ["data", "createProducts"], resCreate);

    alert(` ${countUpdate}    ${countcareate} `);
    setNewPrice([]);
    closeModal();
  };

  return (
    <>
      <BtnWrapper>
        <Button
          disabled={R.isEmpty(diffProducts)}
          loading={loading}
          onClick={handleCreateAndUpdate}
        >
             
        </Button>
        <Button
          disabled={R.isEmpty(updateProductsItems)}
          loading={loading}
          onClick={handleUpdateProducts}
        >
            
        </Button>
      </BtnWrapper>
      <h3>    !</h3>
      <ProductShortTable products={diffProducts} />
    </>
  );
};
