import { useQuery, gql } from "@apollo/client";
import { Table, Button } from "antd";
import {
  ButtonWrapper,
  EditButton,
  SettingsBtn,
} from "../../../components/Button";
import { UpdateSettingsForm } from "../../../components/UpdateSettingsForm";

import { useModalContext } from "../../../providers/ModalProvider";
import { AddProductForm } from "../ProductForm/AddProductForm";
import { EditProductForm } from "../ProductForm/EditProductForm";
import { AddProductSetForm } from "../ProductSetForm.js/AddProductSetForm";
import { EditProductSetForm } from "../ProductSetForm.js/EditProductSetForm";
import { UpdatePriceExcel } from "../UpdatePrice/UpdatePriceExcel";
import { DeletePriceBtn } from "./DeletePriceBtn";
import { ExcelExport } from "./ExcelExport";
import {
  preparepProductData,
  preparedProductSetData,
  getCategoriesOptions,
  getCategoryFiletrOptions,
} from "./priceTableUtils";
import { priceTypes } from "./priceTypes";
import { getColumnSearchProps } from "./textFilter";

export const categoryFragment = gql`
  fragment categoryFragment on Category {
    _id
    name
    markup
    subcategories {
      _id
      name
      markup
    }
  }
`;

export const productFragment = gql`
  fragment productFragment on Product {
    _id
    nomenclature
    article
    count
    price
    subcategoryId
    markup
    sellingPrice
    category {
      ...categoryFragment
    }
  }
  ${categoryFragment}
`;

export const PRODUCTS_QUERY = gql`
  query {
    products {
      ...productFragment
    }
    productSets {
      _id
      nomenclature
      article
      subcategoryId
      category {
        ...categoryFragment
      }
      products {
        ...productFragment
        category {
          ...categoryFragment
        }
      }
    }
  }
  ${productFragment},
  ${categoryFragment}
`;

export const CATEGORY_QUERY = gql`
  query {
    categories {
      name
      subcategories {
        name
      }
    }
  }
`;

export const PriceTable = () => {
  const { data, loading, refetch } = useQuery(PRODUCTS_QUERY, {
    fetchPolicy: "network-only",
  });

  const { data: categoriesData } = useQuery(CATEGORY_QUERY, {
    fetchPolicy: "network-only",
  });

  const { categories, subCategories } = getCategoriesOptions(categoriesData);

  const productData = preparepProductData(data);
  const productSetsData = preparedProductSetData(data);
  const { showModal, handleCancel } = useModalContext();

  const showAddProductForm = () =>
    showModal({
      title: "  ",
      content: () => (
        <AddProductForm closeModal={handleCancel} refetch={refetch} />
      ),
    });

  const showSettingsForm = () =>
    showModal({
      title: " ",
      content: () => <UpdateSettingsForm closeModal={handleCancel} />,
    });

  const showAddProductSetForm = () =>
    showModal({
      title: "  ",
      width: 800,
      content: () => (
        <AddProductSetForm closeModal={handleCancel} refetch={refetch} />
      ),
    });

  const showEditPriceModal = (id, { nomenclature, type }) => {
    if (type === priceTypes.PRODUCT) {
      showModal({
        title: `  ${nomenclature}`,
        content: () => (
          <EditProductForm
            closeModal={handleCancel}
            refetch={refetch}
            id={id}
          />
        ),
      });
    } else {
      showModal({
        title: `  ${nomenclature}`,
        width: 800,
        content: () => (
          <EditProductSetForm
            closeModal={handleCancel}
            refetch={refetch}
            id={id}
          />
        ),
      });
    }
  };

  const showUpdateProductsModal = () => {
    showModal({
      title: " ",
      width: 800,
      content: () => (
        <UpdatePriceExcel
          oldProductPrice={productData}
          closeModal={handleCancel}
          refetch={refetch}
        />
      ),
    });
  };

  const columnsConfig = [
    {
      title: "",
      dataIndex: "nomenclature",
      ...getColumnSearchProps("nomenclature"),
    },
    {
      title: "",
      dataIndex: "article",

      ...getColumnSearchProps("article"),
      sorter: (a, b) => a.article.localeCompare(b.article),
    },
    {
      title: "",
      dataIndex: "count",
      sorter: (a, b) => b.count - a.count,
    },
    {
      title: " ",
      dataIndex: "price",
      sorter: (a, b) => b.price - a.price,
    },
    {
      title: "",
      dataIndex: "dropPrice",
      sorter: (a, b) => b.dropPrice - a.dropPrice,
    },
    {
      title: "",
      dataIndex: "sellingPrice",
      sorter: (a, b) => b.sellingPrice - a.sellingPrice,
    },
    {
      title: "",
      dataIndex: "wholesalePrice",
      sorter: (a, b) => b.wholesalePrice - a.wholesalePrice,
    },
    {
      title: "",
      dataIndex: "type",
      filters: [
        {
          text: priceTypes.PRODUCT,
          value: priceTypes.PRODUCT,
        },
        {
          text: priceTypes.PRODUCT_SET,
          value: priceTypes.PRODUCT_SET,
        },
      ],
      onFilter: (value, { type }) => type === value,
    },
    {
      title: "",
      dataIndex: "categoryName",
      filters: getCategoryFiletrOptions(categories),
      onFilter: (value, { categoryName }) => categoryName === value,
    },
    {
      title: " ",
      dataIndex: "subcategoryName",
      filters: getCategoryFiletrOptions(subCategories),
      onFilter: (value, { subcategoryName }) => subcategoryName === value,
    },
    {
      dataIndex: "_id",
      render: (id, price) => (
        <ButtonWrapper>
          <EditButton onClick={() => showEditPriceModal(id, price)} />
          <DeletePriceBtn
            refetch={refetch}
            id={id}
            nomenclature={price.nomenclature}
            type={price.type}
          />
        </ButtonWrapper>
      ),
    },
  ];

  return (
    <>
      <ButtonWrapper>
        <Button type="primary" onClick={showAddProductForm}>
           
        </Button>
        <Button type="primary" onClick={showAddProductSetForm}>
           
        </Button>
        <ExcelExport price={[...productData, ...productSetsData]} />

        <Button type="primary" onClick={showUpdateProductsModal}>
           
        </Button>

        <p>: {productData.length} . </p>
        <p>: {productSetsData.length} . </p>
        <SettingsBtn onClick={showSettingsForm} />
      </ButtonWrapper>

      <Table
        columns={columnsConfig}
        dataSource={[...productData, ...productSetsData]}
        loading={loading}
        rowKey="_id"
        pagination={{ showTotal: (total) => ` ${total} .` }}
        size="small"
      />
    </>
  );
};
