import { useQuery } from "@apollo/client";
import { Table } from "antd";
import * as R from "ramda";
import moment from "moment";

import { useParams } from "react-router-dom";
import styled from "styled-components";

import {
  BATCH_ORDER_QUERY,
  calculateBatchOrderTotal,
} from "./batchOrder/BatchOrderTable";
import { useModalContext } from "../providers/ModalProvider";
import { ProductsOrderTable } from "../components/ProductsOrderTable";
import { OrderDetailBtn } from "../components/Button";

const InfoWrapper = styled.div`
  display: flex;
  align-items: center;
  gap: 20px;
`;

export const Wrapper = styled.div`
  padding: 10px;
`;

export const PublicBatchOrder = () => {
  const { batchOrderId } = useParams();

  const { showModal } = useModalContext();

  const { data, loading } = useQuery(BATCH_ORDER_QUERY, {
    variables: {
      id: batchOrderId,
    },
    skip: !batchOrderId,
    fetchPolicy: "network-only",
  });

  const batchOrderName = R.pathOr("", ["batchOrder", "name"], data);
  const dropBuyerName = R.pathOr("", ["batchOrder", "dropBuyer", "name"], data);
  const batchMovedTotal = R.pathOr("", ["batchOrder", "movedTotal"], data);

  const orders = R.compose(
    R.reverse,
    R.sortBy(R.prop("date")),
    R.pathOr([], ["batchOrder", "orders"])
  )(data);

  const total = calculateBatchOrderTotal(batchMovedTotal)(orders);

  const showOrderDetailModal = (products) =>
    showModal({
      title: "",
      width: 1000,
      content: () => (
        <ProductsOrderTable products={products} type="public-drop" />
      ),
    });

  const columnsConfig = [
    {
      title: "",
      dataIndex: "date",
      render: (value) => moment(value).format("YYYY-MM-DD"),
    },
    {
      title: "",
      dataIndex: "buyerInfo",
    },
    {
      title: "",
      dataIndex: "ttn",
    },

    {
      title: " ",
      dataIndex: "totalDropPrice",
    },

    {
      title: "",
      dataIndex: "payed",
    },

    {
      title: "",
      dataIndex: "status",
    },

    {
      dataIndex: "_id",
      render: (_, order) => (
        <OrderDetailBtn
          onClick={() => showOrderDetailModal(R.propOr([], "products", order))}
        />
      ),
    },
  ];

  return (
    <Wrapper>
      <InfoWrapper>
        <p>: {batchOrderName}</p>
        <p>: {dropBuyerName}</p>
        <p>: {batchMovedTotal} </p>

        <h3>
          <b>: {total} </b>
        </h3>
      </InfoWrapper>

      <Table
        columns={columnsConfig}
        dataSource={orders}
        loading={loading}
        rowKey="id"
        pagination={false}
        size="small"
      />
    </Wrapper>
  );
};
