import * as R from "ramda";

import "./App.css";
import { ModalProvider } from "./providers/ModalProvider";
import { AppRouter } from "./routes/AppRouter";
import { UserProvider } from "./providers/UserProvider";
import { gql, useQuery } from "@apollo/client";

const SETTINGS_QUERY = gql`
  query settings {
    settings {
      dropshippingMarkup
    }
  }
`;

export const App = () => {
  const { data, loading } = useQuery(SETTINGS_QUERY, {
    fetchPolicy: "network-only",
  });

  if (loading) {
    return <p>Loading...</p>;
  }

  const dropshippingMarkup = R.path(["settings", "dropshippingMarkup"], data);

  if (dropshippingMarkup > 0) {
    localStorage.setItem("setting", dropshippingMarkup);
  }

  return (
    <UserProvider>
      <ModalProvider>
        <AppRouter />
      </ModalProvider>
    </UserProvider>
  );
};
