import { useMutation, gql } from "@apollo/client";
import * as R from "ramda";
import moment from "moment";

import { prepareValuesBeforeSubmit } from "./AddOrderForm";
import { OrderForm } from "./OrderForm";

const UPDATE_ORDER = gql`
  mutation updateOrder($batchOrderId: ID!, $orderId: ID!, $order: OrderInput!) {
    updateOrder(batchOrderId: $batchOrderId, orderId: $orderId, order: $order) {
      _id
    }
  }
`;

const getProductIds = R.compose(R.map(R.prop("_id")), R.propOr([], "products"));

export const UpdateOrdersForm = ({
  closeModal,
  refetch,
  batchOrderId,
  order,
}) => {
  console.log(order);
  const [updateOrder] = useMutation(UPDATE_ORDER);

  const productIds = getProductIds(order);

  const initialValues = { ...order, productIds, date: moment(order.date) };

  const initialProducts = R.propOr([], "products", order);

  const handleSubmit = async (values) => {
    try {
      updateOrder({
        variables: {
          batchOrderId,
          orderId: order.id,
          order: { ...prepareValuesBeforeSubmit(values) },
        },
      });
      closeModal();
      refetch();
    } catch (e) {
      alert(e);
    }
  };

  return (
    <OrderForm
      onFinish={handleSubmit}
      initialValues={initialValues}
      initialProducts={initialProducts}
    />
  );
};
