import { Form, Input, Checkbox, InputNumber } from "antd";

import { requiredRule, SubmitBtnItem } from "../../../components/Form";
import { DropBuyerSelect } from "./DropBuyerSelect";

export const BatchOrderForm = ({
  onFinish,
  initialValues = { isActive: true },
}) => {
  return (
    <Form
      name="batchOrderForm"
      onFinish={onFinish}
      layout={"vertical"}
      initialValues={initialValues}
    >
      <Form.Item label="" name="name" rules={[requiredRule]}>
        <Input />
      </Form.Item>

      <DropBuyerSelect />

      <Form.Item name="isActive" valuePropName="checked">
        <Checkbox></Checkbox>
      </Form.Item>
      <Form.Item
        label=" "
        name="movedTotal"
        rules={[requiredRule]}
      >
        <InputNumber />
      </Form.Item>

      <SubmitBtnItem />
    </Form>
  );
};
