import { useQuery, gql } from "@apollo/client";
import { Table, Button } from "antd";
import * as R from "ramda";
import { useState } from "react";
import { Select } from "antd";

import { useModalContext } from "../../providers/ModalProvider";
import { AddBatchOrdersForm } from "./from/AddBatchOrdersForm";
import { EditBatchOrdersForm } from "./from/EditBatchOrdersForm";
import { ButtonWrapper, EditButton } from "../../components/Button";
import { generatePath, Link } from "react-router-dom";
import { routes } from "../../routes/config";
import { DeleteBatchBtn } from "./DeleteBatchBtn";

const { Option } = Select;

export const BATCH_ORDERS_QUERY = gql`
  query batchOrdersQuery($query: BatchOrdersQuery!) {
    batchOrders(query: $query) {
      _id
      name
      dropBuyer {
        _id
        name
      }
      isActive
      movedTotal
    }
  }
`;

export const BatchOrdersTable = ({ query }) => {
  const [internalQuery, setInternalQuery] = useState(query);

  const { showModal, handleCancel } = useModalContext();

  const { data, loading, refetch } = useQuery(BATCH_ORDERS_QUERY, {
    variables: {
      query: internalQuery,
    },
    skip: !internalQuery,
    fetchPolicy: "network-only",
  });

  const showEditBatchOrderModal = (batchOrder) =>
    showModal({
      title: `  ${batchOrder.name}`,
      content: () => (
        <EditBatchOrdersForm
          closeModal={handleCancel}
          refetch={refetch}
          batchOrder={batchOrder}
        />
      ),
    });

  const columnsConfig = [
    {
      title: "",
      dataIndex: "name",
      render: (name, batchOrder) => {
        const path = generatePath(routes.batchOrder, {
          batchOrderId: batchOrder._id,
        });
        return <Link to={path}>{name}</Link>;
      },
    },
    {
      title: "",
      dataIndex: "dropBuyer",
      render: (dropBuyer) => dropBuyer.name,
    },
    {
      title: "",
      dataIndex: "isActive",
      render: (isActive) => (isActive ? "" : ""),
    },
    {
      dataIndex: "_id",
      render: (id, batchOrder) => (
        <ButtonWrapper>
          <EditButton onClick={() => showEditBatchOrderModal(batchOrder)} />
          <DeleteBatchBtn
            refetch={refetch}
            id={id}
            batchName={batchOrder.name}
          />
        </ButtonWrapper>
      ),
    },
  ];

  const handleChangeBatchFilter = (value) => {
    setInternalQuery({ ...internalQuery, isActive: value });
  };

  const showAddBatchOrderModal = () =>
    showModal({
      title: "   ",
      content: () => (
        <AddBatchOrdersForm closeModal={handleCancel} refetch={refetch} />
      ),
    });

  return (
    <>
      <Select
        defaultValue={true}
        style={{ width: 120, margin: "10px" }}
        onChange={handleChangeBatchFilter}
      >
        <Option value={true}></Option>
        <Option value={false}> </Option>
      </Select>
      <Button
        style={{ margin: "10px" }}
        type="primary"
        onClick={showAddBatchOrderModal}
      >
          
      </Button>
      <Table
        columns={columnsConfig}
        dataSource={R.compose(
          R.reverse,
          R.sortBy(R.prop("name")),
          R.propOr([], "batchOrders")
        )(data)}
        loading={loading}
        rowKey="_id"
        pagination={false}
        size="small"
      />
    </>
  );
};
