import { useMutation, gql } from "@apollo/client";
import * as R from "ramda";
import { roundToKopeck } from "../../price/PriceTable/priceTableUtils";
import { OwnOrderForm } from "./OwnOrderForm";

const CREATE_OWN_ORDER = gql`
  mutation createOwnOrder($input: OwnOrderInput!) {
    createOwnOrder(input: $input) {
      _id
    }
  }
`;

export const prepareValuesBeforeSubmit = R.compose(
  R.converge(R.mergeRight, [
    R.identity,
    R.compose(
      R.objOf("products"),
      R.map(
        R.applySpec({
          _id: R.prop("_id"),
          article: R.prop("article"),
          count: R.prop("count"),
          price: R.compose(roundToKopeck, R.prop("price")),
          sellingPrice: R.compose(roundToKopeck, R.prop("sellingPrice")),
          nomenclature: R.prop("nomenclature"),
        })
      ),
      R.propOr([], "products")
    ),
  ]),
  R.dissoc("productIds")
);

export const AddOwnOrdersForm = ({ closeModal, refetch }) => {
  const [createOwnOrder] = useMutation(CREATE_OWN_ORDER);

  const handleSubmit = async (values) => {
    try {
      await createOwnOrder({
        variables: {
          input: prepareValuesBeforeSubmit(values),
        },
      });
      closeModal();
      refetch();
    } catch (e) {
      alert(e);
    }
  };

  return <OwnOrderForm onFinish={handleSubmit} />;
};
