import { useQuery, gql } from "@apollo/client";
import { Form, Input, Select } from "antd";
import * as R from "ramda";
import { useState } from "react";

import {
  ArticleInputItem,
  requiredRule,
  SubmitBtnItem,
} from "../../../components/Form";
import { ProductShortTable } from "../../../components/ProductShortTable";
import { CategorySelectItem, useCategoryId } from "../ProductForm/ProductForm";

// findSelectetdProduct :: [ID] -> [Products] -> Product
const findSelectetdProduct = R.curry((ids, products) =>
  R.find(R.propEq("_id", R.last(ids)), products)
);

const filterSelectedProducts = R.curry((ids, products) =>
  R.filter(R.compose(R.includes(R.__, ids), R.prop("_id")), products)
);

const SEARCH_PRODUCTS = gql`
  query productsSearch($search: String!) {
    productsSearch(search: $search) {
      _id
      nomenclature
      article
      price
      count
    }
  }
`;

const ProductSelectItem = ({ initialProducts }) => {
  const [selectedProducts, setSelectedProducts] = useState(initialProducts);

  const [search, setSearch] = useState("");

  const { data } = useQuery(SEARCH_PRODUCTS, {
    variables: {
      search,
    },
    skip: search.length < 3,
  });

  const products = R.uniq([
    ...R.propOr([], "productsSearch", data),
    ...initialProducts,
  ]);

  const onSearch = (value) => {
    setSearch(value);
  };

  const onChange = (productIds) => {
    if (productIds.length > selectedProducts.length) {
      setSelectedProducts([
        ...selectedProducts,
        findSelectetdProduct(productIds, products),
      ]);
    } else {
      setSelectedProducts(filterSelectedProducts(productIds, selectedProducts));
    }
  };

  return (
    <>
      <Form.Item label="" name="productIds" rules={[requiredRule]}>
        <Select
          onChange={onChange}
          onSearch={onSearch}
          mode="multiple"
          filterOption={(input, option) =>
            option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
          }
        >
          {products.map((product) => (
            <Select.Option key={product._id} value={product._id}>
              {product.nomenclature}
            </Select.Option>
          ))}
        </Select>
      </Form.Item>

      <ProductShortTable products={selectedProducts} />
      <br></br>
    </>
  );
};

export const ProductSetForm = ({
  onFinish,
  initialValues = {},
  initialProducts = [],
}) => {
  const { categoryId, onValuesChange } = useCategoryId(initialValues);
  return (
    <Form
      name="productForm"
      onFinish={onFinish}
      layout={"vertical"}
      initialValues={initialValues}
      onValuesChange={onValuesChange}
    >
      <Form.Item
        label=""
        name="nomenclature"
        rules={[requiredRule]}
      >
        <Input />
      </Form.Item>
      <CategorySelectItem categoryId={categoryId} />
      <ArticleInputItem />
      <ProductSelectItem initialProducts={initialProducts} />
      <SubmitBtnItem />
    </Form>
  );
};
