import { Routes, Route } from "react-router-dom";
import { routesConfig } from "./config";

export const AppRouter = () => {
  return (
    <Routes>
      {routesConfig.map(({ routes, rootPath, Layout }) => (
        <Route key={rootPath} path={rootPath} element={<Layout />}>
          {routes.map(({ path, index, Component }) => (
            <Route
              index={index}
              key={path}
              path={path}
              element={<Component />}
            />
          ))}
        </Route>
      ))}
    </Routes>
  );
};
