import { useMutation, gql, useQuery } from "@apollo/client";

import { categoryFragment } from "../../price/PriceTable/PriceTable";
import { CategoryForm } from "./CategoryForm";
import {
  prepareCategoryIntialValues,
  prepareUpdateCategoryBeforSubmit,
} from "./utils";

const UPDATE_CATEGORY_MUTATION = gql`
  mutation updateCategory($updateCategoryId: ID!, $update: CategoryInput!) {
    updateCategory(id: $updateCategoryId, update: $update) {
      _id
    }
  }
`;

const CATEGORY_QUERY = gql`
  query category($id: ID!) {
    category(id: $id) {
      ...categoryFragment
    }
  }
  ${categoryFragment}
`;

export const EditCategoryForm = ({ id, closeModal, refetch }) => {
  const { data, loading } = useQuery(CATEGORY_QUERY, {
    variables: { id },
    fetchPolicy: "network-only",
  });

  const initialValues = prepareCategoryIntialValues(data);

  const [updateCategory] = useMutation(UPDATE_CATEGORY_MUTATION);

  const handleSubmit = async (values) => {
    try {
      await updateCategory({
        variables: {
          updateCategoryId: id,
          update: prepareUpdateCategoryBeforSubmit(values),
        },
      });
      closeModal();
      refetch();
    } catch (e) {
      alert(e);
    }
  };

  if (loading) return null;

  return <CategoryForm onFinish={handleSubmit} initialValues={initialValues} />;
};
