import { useMutation, gql } from "@apollo/client";
import { removeSubcategoriIsNeed } from "../ProductForm/utils";

import { ProductSetForm } from "./ProductSetForm";

const ADD_PRODUCT_SET_MUTATION = gql`
  mutation createProductSet($input: ProductSetInput!) {
    createProductSet(input: $input) {
      _id
    }
  }
`;

export const AddProductSetForm = ({ closeModal, refetch }) => {
  const [addProductSet] = useMutation(ADD_PRODUCT_SET_MUTATION);

  const handleSubmit = async (values) => {
    await addProductSet({
      variables: {
        input: removeSubcategoriIsNeed(values),
      },
    });
    closeModal();
    refetch();
  };

  return <ProductSetForm onFinish={handleSubmit} />;
};
