import * as R from "ramda";

// isSubcategoryNill :: Category -> Boolean
const isSubcategoryNill = R.compose(R.isNil, R.prop("subcategoryId"));

// removeSubcategoriIsNeed :: ProductFormData -> ProductInput
export const removeSubcategoriIsNeed = R.when(
  isSubcategoryNill,
  R.dissoc("subcategoryId")
);

export const prepareCategoryId = R.converge(R.mergeRight, [
  R.identity,
  R.applySpec({
    categoryId: R.path(["category", "_id"]),
  }),
]);

// prepareProductInitialValues :: QueryProductData -> InitialProductValue
export const prepareProductInitialValues = R.compose(
  prepareCategoryId,
  R.dissoc("__typename"),
  R.propOr({}, "product")
);

// prepareProductBeforeSubmit :: ProductFormData -> ProductInput
export const prepareProductBeforeSubmit = R.when(
  isSubcategoryNill,
  R.mergeLeft({ subcategoryId: null })
);
