import { Menu } from "antd";
import { useState } from "react";
import { useNavigate } from "react-router";
import { useLocation } from "react-router-dom";
import styled from "styled-components";
import { useUserContext } from "../providers/UserProvider";

import { routes } from "../routes/config";

const ExitBtn = styled.div`
  cursor: pointer;
  float: right;
  &:hover {
    color: #fff;
  }
`;

const UserNameInfo = styled.span`
  margin: 0 20px;
`;

export const AppMenu = () => {
  const location = useLocation();
  const [current, setCurrent] = useState(location.pathname);

  const { user, userLogout } = useUserContext();

  const navigate = useNavigate();

  const handleClick = (e) => {
    setCurrent(e.key);
    navigate(e.key);
  };

  return (
    <Menu
      onClick={handleClick}
      selectedKeys={[current]}
      mode="horizontal"
      theme="dark"
    >
      <Menu.Item key={routes.home}></Menu.Item>
      <Menu.Item key={routes.category}></Menu.Item>
      <Menu.Item key={routes.dropBuyer}></Menu.Item>
      <Menu.Item key={routes.batchOrders}></Menu.Item>
      <Menu.Item key={routes.statistic}></Menu.Item>
      <Menu.Item key={routes.ownOrders}> </Menu.Item>
      <Menu.Item
        style={{ marginLeft: "auto" }}
        key={routes.login}
        onClick={userLogout}
      >
        <UserNameInfo>: {user?.username}</UserNameInfo>
        <ExitBtn></ExitBtn>
      </Menu.Item>
    </Menu>
  );
};
