import * as R from "ramda";

import { priceTypes } from "./priceTypes";

const EMPTT_VALUE = "-";

const DROPSHIPPING_MARKUP = localStorage.getItem("setting") || 10;
const WHOLESALE_MARKUP = -5;

// fixedFloat :: Number -> Number -> Number
const fixedFloat = (to) => R.invoker(1, "toFixed")(to);

export const roundToKopeck = (value) =>
  Math.round((value + Number.EPSILON) * 100) / 100;

// calculateMarkup :: Number -> Number
const calculateMarkup = R.curry((markup, price) =>
  R.compose(
    fixedFloat(2),
    R.multiply(R.add(100, markup)),
    R.divide(R.__, 100)
  )(price)
);

// findProductSubCategory :: Product -> Subcategory
const findProductSubCategory = R.converge(R.find, [
  R.compose(R.propEq("_id"), R.propOr(undefined, "subcategoryId")),
  R.pathOr([], ["category", "subcategories"]),
]);

const commonProductFields = {
  _id: R.prop("_id"),
  nomenclature: R.propOr(EMPTT_VALUE, "nomenclature"),
  article: R.propOr(EMPTT_VALUE, "article"),
  categoryName: R.pathOr(EMPTT_VALUE, ["category", "name"]),
  subcategoryName: R.compose(
    R.propOr(EMPTT_VALUE, "name"),
    findProductSubCategory
  ),
};

// preparepProductData :: ProductQueryData -> [PreparedProduct]
export const preparepProductData = R.compose(
  R.map(
    R.compose(
      R.converge(R.mergeRight, [
        R.identity,
        R.applySpec({
          wholesalePrice: R.compose(
            Math.round,
            calculateMarkup(WHOLESALE_MARKUP),
            R.propOr(0, "sellingPrice")
          ),
        }),
      ]),
      R.applySpec({
        ...commonProductFields,
        count: R.propOr(EMPTT_VALUE, "count"),
        price: R.propOr(EMPTT_VALUE, "price"),
        dropPrice: R.compose(
          parseFloat,
          calculateMarkup(DROPSHIPPING_MARKUP),
          R.propOr(0, "price")
        ),
        sellingPrice: R.prop("sellingPrice"),
        type: R.always(priceTypes.PRODUCT),
        category: R.propOr({}, "category"),
        markup: R.propOr(null, "markup"),
      })
    )
  ),
  R.propOr([], "products")
);

const getProducts = R.propOr([], "products");

const diff = (a, b) => a - b;

export const preparedProductSetData = R.compose(
  R.map(
    R.applySpec({
      ...commonProductFields,
      price: R.compose(roundToKopeck, R.sum, R.pluck("price"), getProducts),
      count: R.compose(R.head, R.sort(diff), R.pluck("count"), getProducts),
      dropPrice: R.compose(
        roundToKopeck,
        parseFloat,
        R.sum,
        R.pluck("dropPrice"),
        getProducts
      ),
      sellingPrice: R.compose(
        Math.round,
        R.sum,
        R.pluck("sellingPrice"),
        getProducts
      ),
      wholesalePrice: R.compose(
        Math.round,
        R.sum,
        R.pluck("wholesalePrice"),
        getProducts
      ),
      type: R.always(priceTypes.PRODUCT_SET),
    })
  ),
  R.map(
    R.converge(R.mergeRight, [
      R.identity,
      R.compose(R.objOf("products"), preparepProductData),
    ])
  ),
  R.propOr([], "productSets")
);

export const getCategoriesOptions = R.compose(
  R.applySpec({
    categories: R.compose(R.uniq, R.pluck("name")),
    subCategories: R.compose(
      R.reject(R.isNil),
      R.uniq,
      R.pluck("name"),
      R.unnest,
      R.pluck("subcategories")
    ),
  }),
  R.propOr([], "categories")
);

export const getCategoryFiletrOptions = R.compose(
  R.append({
    text: EMPTT_VALUE,
    value: EMPTT_VALUE,
  }),
  R.map(
    R.applySpec({
      text: R.identity,
      value: R.identity,
    })
  )
);
