import { Form, Input, InputNumber, Space, Select, DatePicker } from "antd";
import * as R from "ramda";

import { requiredRule, SubmitBtnItem } from "../../../components/Form";
import { useUserContext } from "../../../providers/UserProvider";
import { ProductItems } from "./ProductItems";

export const ORDER_STATUSES = {
  PAID_BEFORE: "",
  PAID_AFTER: "",
  PAID: " ",
};

export const OrderForm = ({
  onFinish,
  initialValues = { isActive: true },
  initialProducts = [],
}) => {
  const [form] = Form.useForm();
  const { user } = useUserContext();
  const isAdmin = user.role === "ADMIN";

  return (
    <Form
      form={form}
      name="orderForm"
      onFinish={onFinish}
      layout={"vertical"}
      initialValues={initialValues}
    >
      <Form.Item name="date" label="" rules={[requiredRule]}>
        <DatePicker />
      </Form.Item>
      <Form.Item label="" name="ttn">
        <Input />
      </Form.Item>
      <Form.Item
        label=""
        name="status"
        initialValue={ORDER_STATUSES.PAID_BEFORE}
      >
        <Select>
          {R.values(ORDER_STATUSES).map((status) => (
            <Select.Option key={status} value={status}>
              {status}
            </Select.Option>
          ))}
        </Select>
      </Form.Item>
      <Form.Item label="" name="buyerInfo">
        <Input />
      </Form.Item>
      <ProductItems form={form} initialProducts={initialProducts} />
      <Space>
        <Form.Item label="" name="payed">
          <InputNumber />
        </Form.Item>

        <Form.Item
          label=" "
          name="totalDropPrice"
          rules={[requiredRule]}
          initialValue={0}
        >
          <InputNumber min={0} disabled={!isAdmin} />
        </Form.Item>
      </Space>
      <br></br>
      <Space>
        <Form.Item
          name="totalPrice"
          label="  "
          rules={[requiredRule]}
          initialValue={0}
        >
          <InputNumber min={0} disabled={!isAdmin} />
        </Form.Item>
        <Form.Item
          name="totalSellPrice"
          label=" "
          rules={[requiredRule]}
          initialValue={0}
        >
          <InputNumber min={0} disabled={!isAdmin} />
        </Form.Item>
      </Space>

      <SubmitBtnItem />
    </Form>
  );
};
