import { useState } from "react";

import { useQuery, gql } from "@apollo/client";
import { Form, Select, Space, Input, InputNumber } from "antd";
import * as R from "ramda";
import { requiredRule } from "../../../components/Form";
import {
  preparedProductSetData,
  preparepProductData,
  roundToKopeck,
} from "../../price/PriceTable/priceTableUtils";
import { useUserContext } from "../../../providers/UserProvider";

export const SEARCH_PRODUCTS = gql`
  query productsSearch($search: String!) {
    productsSearch(search: $search) {
      _id
      nomenclature
      article
      price
      markup
      sellingPrice
      category {
        _id
        name
        markup
        subcategories {
          _id
          name
          markup
        }
      }
    }
    productSetSearch(search: $search) {
      _id
      nomenclature
      article
      products {
        _id
        nomenclature
        article
        price
        markup
        sellingPrice
        category {
          _id
          name
          markup
          subcategories {
            _id
            name
            markup
          }
        }
      }
      category {
        _id
        name
        markup
        subcategories {
          _id
          name
          markup
        }
      }
      subcategoryId
    }
  }
`;

const filterSelectedProducts = R.curry((ids, products) =>
  R.filter(R.compose(R.includes(R.__, ids), R.prop("_id")), products)
);

const calculateTotal = R.compose(
  R.applySpec({
    products: R.identity,
    totalPrice: R.compose(
      roundToKopeck,
      R.sum,
      R.map(R.propOr(0, "totalPrice"))
    ),
    totalDropPrice: R.compose(
      roundToKopeck,
      R.sum,
      R.map(R.propOr(0, "totalDropPrice"))
    ),
    totalSellPrice: R.compose(
      roundToKopeck,
      R.sum,
      R.map(R.propOr(0, "totalSellingPrice"))
    ),
  }),
  R.map(
    R.converge(R.mergeRight, [
      R.identity,
      R.applySpec({
        totalPrice: R.converge(R.multiply, [R.prop("price"), R.prop("count")]),
        totalDropPrice: R.converge(R.multiply, [
          R.prop("dropPrice"),
          R.prop("count"),
        ]),
        totalSellingPrice: R.converge(R.multiply, [
          R.prop("sellingPrice"),
          R.prop("count"),
        ]),
      }),
    ])
  )
);

const prepareProductData = R.compose(
  preparepProductData,
  R.objOf("products"),
  R.map(
    R.converge(R.mergeRight, [
      R.identity,
      R.applySpec({ count: R.propOr(1, "count") }),
    ])
  ),
  R.propOr([], "productsSearch")
);

const preparedProductSetsData = R.compose(
  R.map(
    R.converge(R.mergeRight, [
      R.identity,
      R.applySpec({
        count: R.ifElse(R.propEq("count", "-"), R.always(1), R.prop("count")),
      }),
    ])
  ),
  preparedProductSetData,
  R.objOf("productSets"),

  R.propOr([], "productSetSearch")
);

export const ProductItems = ({ form, initialProducts = [] }) => {
  const { user } = useUserContext();

  const isAdmin = user.role === "ADMIN";
  const [selectedProducts, setSelectedProducts] = useState(initialProducts);

  const [search, setSearch] = useState("");

  const { data } = useQuery(SEARCH_PRODUCTS, {
    variables: {
      search,
    },
    skip: search.length < 3,
  });

  const onSearch = (value) => {
    setSearch(value);
  };

  const onChange = (productIds) => {
    let newProducts = [];

    if (productIds.length > selectedProducts.length) {
      const productItems = prepareProductData(data);
      const setProductsItems = preparedProductSetsData(data);

      const selectedProduct = R.find(R.propEq("_id", R.last(productIds)), [
        ...productItems,
        ...setProductsItems,
      ]);
      newProducts = [...selectedProducts, selectedProduct];
    } else {
      newProducts = filterSelectedProducts(productIds, selectedProducts);
    }

    const newFormCalculateData = calculateTotal(newProducts);

    form.setFieldsValue(newFormCalculateData);
    setSelectedProducts(newFormCalculateData.products);
  };

  const onCountChanged = () => {
    const products = form.getFieldValue("products");
    const newFormData = calculateTotal(products);
    form.setFieldsValue(newFormData);
    setSelectedProducts(newFormData.products);
  };

  const products = R.uniq([
    ...R.propOr([], "productsSearch", data),
    ...R.propOr([], "productSetSearch", data),
    ...initialProducts,
  ]);

  return (
    <>
      <Form.Item label="()" name="productIds">
        <Select
          onChange={onChange}
          onSearch={onSearch}
          mode="multiple"
          filterOption={(input, option) =>
            option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
          }
        >
          {products.map((product) => (
            <Select.Option key={product._id} value={product._id}>
              {product.article}
            </Select.Option>
          ))}
        </Select>
      </Form.Item>

      <Form.List name="products">
        {(fields, { add, remove }) => (
          <>
            {fields.map((field) => (
              <Space key={field.key} align="baseline">
                <Form.Item
                  noStyle
                  shouldUpdate={(prevValues, curValues) =>
                    prevValues.area !== curValues.area ||
                    prevValues.sights !== curValues.sights
                  }
                >
                  {() => (
                    <Form.Item
                      {...field}
                      label=""
                      name={[field.name, "nomenclature"]}
                      fieldKey={[field.fieldKey, "nomenclature"]}
                      rules={[requiredRule]}
                    >
                      <Input disabled={true} />
                    </Form.Item>
                  )}
                </Form.Item>
                <Form.Item
                  {...field}
                  label=" "
                  name={[field.name, "dropPrice"]}
                  fieldKey={[field.fieldKey, "dropPrice"]}
                  rules={[requiredRule]}
                  onChange={onCountChanged}
                >
                  <InputNumber disabled={!isAdmin} />
                </Form.Item>
                <Form.Item
                  {...field}
                  label=""
                  name={[field.name, "count"]}
                  fieldKey={[field.fieldKey, "count"]}
                  rules={[requiredRule]}
                  onChange={onCountChanged}
                >
                  <InputNumber />
                </Form.Item>
              </Space>
            ))}
          </>
        )}
      </Form.List>

      <br></br>
    </>
  );
};
