import { useMutation, gql } from "@apollo/client";

import { DeleteButton } from "../../../components/Button";
import { useConfirmDeleteModal } from "../../../hook/useCinfirmModal";
import { priceTypes } from "./priceTypes";

const DELETE_PRODUCT_MUTATION = gql`
  mutation deleteProduct($priceId: ID!) {
    deleteProduct(id: $priceId)
  }
`;

const DELETE_PRODUCTSET_MUTATION = gql`
  mutation deleteProductSet($priceId: ID!) {
    deleteProductSet(id: $priceId)
  }
`;

export const DeletePriceBtn = ({ id, nomenclature, refetch, type }) => {
  const DELETE_MUTATION =
    type === priceTypes.PRODUCT
      ? DELETE_PRODUCT_MUTATION
      : DELETE_PRODUCTSET_MUTATION;

  const [deletePice] = useMutation(DELETE_MUTATION, {
    variables: { priceId: id },
  });

  const showDeleteConfirmModal = useConfirmDeleteModal(
    nomenclature,
    deletePice,
    refetch
  );

  return <DeleteButton onClick={showDeleteConfirmModal} />;
};
