import { useMutation, gql } from "@apollo/client";

import { ProductForm } from "./ProductForm";
import { removeSubcategoriIsNeed } from "./utils";

const ADD_PRODUCT_MUTATION = gql`
  mutation createProduct($input: ProductInput!) {
    createProduct(input: $input) {
      _id
    }
  }
`;

export const AddProductForm = ({ closeModal, refetch }) => {
  const [addProduct] = useMutation(ADD_PRODUCT_MUTATION);

  const handleSubmit = async (values) => {
    try {
      await addProduct({
        variables: {
          input: removeSubcategoriIsNeed(values),
        },
      });
      closeModal();
      refetch();
    } catch (e) {
      alert(e);
    }
  };

  return <ProductForm onFinish={handleSubmit} />;
};
