import { Table } from "antd";

export const ProductsOrderTable = ({ type, products }) => {
  const baseColumns = [
    {
      title: "",
      dataIndex: "nomenclature",
    },
    {
      title: "",
      dataIndex: "article",
    },
    {
      title: "",
      dataIndex: "count",
    },
  ];

  let columns = [];

  if (type === "own") {
    columns = [
      ...baseColumns,
      {
        title: " ",
        dataIndex: "price",
      },
      {
        title: "",
        dataIndex: "sellingPrice",
      },
    ];
  } else if (type === "public-drop") {
    columns = [
      ...baseColumns,
      {
        title: "",
        dataIndex: "dropPrice",
      },
    ];
  } else if (type === "drop") {
    columns = [
      ...baseColumns,
      {
        title: " ",
        dataIndex: "price",
      },
      {
        title: "",
        dataIndex: "dropPrice",
      },
      {
        title: "",
        dataIndex: "sellingPrice",
      },
    ];
  }

  return (
    <Table
      dataSource={products}
      columns={columns}
      pagination={false}
      rowKey="nomenclature"
    />
  );
};
