import * as R from "ramda";
import { Form, Input, Button } from "antd";
import { useMutation, gql } from "@apollo/client";
import { useNavigate } from "react-router";
import { routes } from "../../routes/config";

export const LOGIN_MUTATION = gql`
  mutation LOGIN_MUTATION($input: LoginInput!) {
    login(input: $input) {
      token
    }
  }
`;

export const LoginForm = () => {
  const [login] = useMutation(LOGIN_MUTATION);
  const navigate = useNavigate();

  const onFinish = async (values) => {
    try {
      const response = await login({
        variables: {
          input: values,
        },
      });

      const token = R.pathOr("", ["data", "login", "token"], response);
      localStorage.setItem("token", token);

      setTimeout(() => navigate(routes.home), 500);
    } catch (e) {
      alert(e);
    }
  };

  return (
    <Form
      name="basic"
      labelCol={{
        span: 8,
      }}
      wrapperCol={{
        span: 16,
      }}
      initialValues={{
        remember: true,
      }}
      onFinish={onFinish}
      autoComplete="off"
    >
      <Form.Item
        label=""
        name="username"
        rules={[
          {
            required: true,
            message: "'  '!",
          },
        ]}
      >
        <Input />
      </Form.Item>

      <Form.Item
        label=""
        name="password"
        rules={[
          {
            required: true,
            message: " '!",
          },
        ]}
      >
        <Input.Password />
      </Form.Item>

      <Form.Item
        wrapperCol={{
          offset: 8,
          span: 16,
        }}
      >
        <Button type="primary" htmlType="submit">
          
        </Button>
      </Form.Item>
    </Form>
  );
};
