import { Form, Input, Button, Space, InputNumber } from "antd";
import { MinusCircleOutlined, PlusOutlined } from "@ant-design/icons";
import {
  MarkupInputItem,
  requiredRule,
  SubmitBtnItem,
} from "../../../components/Form";

export const CategoryForm = ({ onFinish, initialValues = {} }) => {
  return (
    <Form
      name="categoryForm"
      onFinish={onFinish}
      layout={"vertical"}
      initialValues={initialValues}
    >
      <Form.Item label="" name="name" rules={[requiredRule]}>
        <Input />
      </Form.Item>
      <MarkupInputItem />
      <Form.List name="subcategories">
        {(fields, { add, remove }) => (
          <>
            {fields.map((field) => (
              <Space key={field.key} align="baseline">
                <Form.Item
                  noStyle
                  shouldUpdate={(prevValues, curValues) =>
                    prevValues.area !== curValues.area ||
                    prevValues.sights !== curValues.sights
                  }
                >
                  {() => (
                    <Form.Item
                      {...field}
                      label=""
                      name={[field.name, "name"]}
                      fieldKey={[field.fieldKey, "sight"]}
                      rules={[requiredRule]}
                    >
                      <Input />
                    </Form.Item>
                  )}
                </Form.Item>
                <Form.Item
                  {...field}
                  label=""
                  name={[field.name, "markup"]}
                  fieldKey={[field.fieldKey, "markup"]}
                >
                  <InputNumber />
                </Form.Item>

                <MinusCircleOutlined onClick={() => remove(field.name)} />
              </Space>
            ))}
            <Form.Item>
              <Button
                type="dashed"
                onClick={() => add()}
                block
                icon={<PlusOutlined />}
              >
                 
              </Button>
            </Form.Item>
          </>
        )}
      </Form.List>
      <SubmitBtnItem />
    </Form>
  );
};
