import { useQuery } from "@apollo/client";
import { Table } from "antd";
import { ButtonWrapper } from "../../components/Button";

import { ExcelExportPublick } from "./PriceTable/ExcelExport";
import { CATEGORY_QUERY, PRODUCTS_QUERY } from "./PriceTable/PriceTable";
import {
  preparepProductData,
  preparedProductSetData,
  getCategoriesOptions,
  getCategoryFiletrOptions,
} from "./PriceTable/priceTableUtils";

import { priceTypes } from "./PriceTable/priceTypes";
import { getColumnSearchProps } from "./PriceTable/textFilter";

export const PublicPricePage = () => {
  const { data, loading } = useQuery(PRODUCTS_QUERY, {
    fetchPolicy: "network-only",
  });

  const { data: categoriesData } = useQuery(CATEGORY_QUERY, {
    fetchPolicy: "network-only",
  });

  const { categories, subCategories } = getCategoriesOptions(categoriesData);

  const productData = preparepProductData(data);
  const productSetsData = preparedProductSetData(data);

  const columnsConfig = [
    {
      title: "",
      dataIndex: "nomenclature",
      ...getColumnSearchProps("nomenclature"),
    },
    {
      title: "",
      dataIndex: "article",
      ...getColumnSearchProps("article"),
    },
    {
      title: "",
      dataIndex: "count",
      sorter: (a, b) => b.count - a.count,
    },
    {
      title: "",
      dataIndex: "dropPrice",
      sorter: (a, b) => b.dropPrice - a.dropPrice,
    },
    {
      title: "",
      dataIndex: "sellingPrice",
      sorter: (a, b) => b.sellingPrice - a.sellingPrice,
    },
    {
      title: "",
      dataIndex: "wholesalePrice",
      sorter: (a, b) => b.wholesalePrice - a.wholesalePrice,
    },
    {
      title: "",
      dataIndex: "type",
      filters: [
        {
          text: priceTypes.PRODUCT,
          value: priceTypes.PRODUCT,
        },
        {
          text: priceTypes.PRODUCT_SET,
          value: priceTypes.PRODUCT_SET,
        },
      ],
      onFilter: (value, { type }) => type === value,
    },
    {
      title: "",
      dataIndex: "categoryName",
      filters: getCategoryFiletrOptions(categories),
      onFilter: (value, { category }) => category === value,
    },
    {
      title: " ",
      dataIndex: "subcategoryName",
      filters: getCategoryFiletrOptions(subCategories),
      onFilter: (value, { subcategory }) => subcategory === value,
    },
  ];

  return (
    <>
      <ButtonWrapper>
        <ExcelExportPublick price={[...productData, ...productSetsData]} />
        <p>: {productData.length} . </p>
        <p>: {productSetsData.length} . </p>
      </ButtonWrapper>

      <Table
        columns={columnsConfig}
        dataSource={[...productData, ...productSetsData]}
        loading={loading}
        rowKey="_id"
        pagination={{ showTotal: (total) => ` ${total} .` }}
      />
    </>
  );
};
