import { Form, Input, Checkbox } from "antd";

import { requiredRule, SubmitBtnItem } from "../../../components/Form";

export const DropBuyerForm = ({
  onFinish,
  initialValues = { isActive: true },
}) => {
  return (
    <Form
      name="dropBuyerForm"
      onFinish={onFinish}
      layout={"vertical"}
      initialValues={initialValues}
    >
      <Form.Item label="" name="name" rules={[requiredRule]}>
        <Input />
      </Form.Item>

      <Form.Item name="isActive" valuePropName="checked">
        <Checkbox></Checkbox>
      </Form.Item>

      <SubmitBtnItem />
    </Form>
  );
};
