import { useMutation, gql, useQuery } from "@apollo/client";
import * as R from "ramda";

import { DropBuyerForm } from "./DropBuyerForm";

const UPDATE_DROPBUYER_MUTATION = gql`
  mutation updatedropBuyer($id: ID!, $update: DropBuyerInput!) {
    updatedropBuyer(id: $id, update: $update) {
      _id
    }
  }
`;

const DROPBUYER_QUERY = gql`
  query dropBuyer($id: ID!) {
    dropBuyer(id: $id) {
      _id
      name
      isActive
    }
  }
`;

export const EditDropBuyerForm = ({ id, closeModal, refetch }) => {
  const { data, loading } = useQuery(DROPBUYER_QUERY, {
    variables: { id },
    fetchPolicy: "network-only",
  });

  const initialValues = R.propOr({}, "dropBuyer", data);

  const [updatedropBuyer] = useMutation(UPDATE_DROPBUYER_MUTATION);

  const handleSubmit = async (values) => {
    try {
      await updatedropBuyer({
        variables: {
          id,
          update: values,
        },
      });
      closeModal();
      refetch();
    } catch (e) {
      alert(e);
    }
  };

  if (loading) return null;

  console.log({ initialValues, id, data });

  return (
    <DropBuyerForm onFinish={handleSubmit} initialValues={initialValues} />
  );
};
