import { Modal } from "antd";
import { ExclamationCircleOutlined } from "@ant-design/icons";

const { confirm } = Modal;

export const useConfirmDeleteModal = (titleName, handleConfirm, refetch) => {
  return () =>
    confirm({
      title: `     ${titleName}`,
      icon: <ExclamationCircleOutlined />,
      okText: "",
      okType: "danger",
      cancelText: "",
      onOk: async () => {
        try {
          await handleConfirm();
          await refetch();
        } catch (e) {
          alert(e);
        }
      },
    });
};
