import { useMutation, gql } from "@apollo/client";
import * as R from "ramda";
import { BatchOrderForm } from "./BatchOrderForm";

const UPDATE_BATCH_ORDERS_MUTATION = gql`
  mutation updateBatchOrders($id: ID!, $update: BatchOrdersInput!) {
    updateBatchOrders(id: $id, update: $update) {
      _id
    }
  }
`;

const getInitialValues = R.applySpec({
  name: R.prop("name"),
  dropBuyerId: R.path(["dropBuyer", "_id"]),
  isActive: R.prop("isActive"),
  movedTotal: R.prop("movedTotal"),
});

export const EditBatchOrdersForm = ({ batchOrder, closeModal, refetch }) => {
  const [updateBatchOrders] = useMutation(UPDATE_BATCH_ORDERS_MUTATION);
  console.log(batchOrder);
  const initialValues = getInitialValues(batchOrder);

  const handleSubmit = async (values) => {
    try {
      await updateBatchOrders({
        variables: {
          id: batchOrder._id,
          update: values,
        },
      });
      closeModal();
      refetch();
    } catch (e) {
      alert(e);
    }
  };

  return (
    <BatchOrderForm onFinish={handleSubmit} initialValues={initialValues} />
  );
};
