import { useMutation, gql } from "@apollo/client";

import { CategoryForm } from "./CategoryForm";
import { prepareCreateCategoryBeforSubmit } from "./utils";

const ADD_CATEGORY_MUTATION = gql`
  mutation createCategory($input: CategoryInput!) {
    createCategory(input: $input) {
      _id
    }
  }
`;

export const AddCategoryForm = ({ closeModal, refetch }) => {
  const [addCategory] = useMutation(ADD_CATEGORY_MUTATION);

  const handleSubmit = async (values) => {
    try {
      await addCategory({
        variables: {
          input: prepareCreateCategoryBeforSubmit(values),
        },
      });
      closeModal();
      refetch();
    } catch (e) {
      alert(e);
    }
  };

  return <CategoryForm onFinish={handleSubmit} />;
};
