import { useMutation, gql } from "@apollo/client";

import { DropBuyerForm } from "./DropBuyerForm";

const ADD_DROPBUYER_MUTATION = gql`
  mutation createdropBuyer($input: DropBuyerInput!) {
    createdropBuyer(input: $input) {
      _id
    }
  }
`;

export const AddDropBuyerForm = ({ closeModal, refetch }) => {
  const [createdropBuyer] = useMutation(ADD_DROPBUYER_MUTATION);

  const handleSubmit = async (values) => {
    try {
      await createdropBuyer({
        variables: {
          input: values,
        },
      });
      closeModal();
      refetch();
    } catch (e) {
      alert(e);
    }
  };

  return <DropBuyerForm onFinish={handleSubmit} />;
};
