import { useMutation, gql } from "@apollo/client";
import * as R from "ramda";
import { roundToKopeck } from "../../price/PriceTable/priceTableUtils";
import { OrderForm } from "./OrderForm";

const ADD_ORDER = gql`
  mutation addOrderToBatch($batchOrderId: ID!, $order: OrderInput!) {
    addOrderToBatch(batchOrderId: $batchOrderId, order: $order) {
      _id
    }
  }
`;

export const prepareValuesBeforeSubmit = R.compose(
  R.converge(R.mergeRight, [
    R.identity,
    R.compose(
      R.objOf("products"),
      R.map(
        R.applySpec({
          _id: R.prop("_id"),
          article: R.prop("article"),
          count: R.prop("count"),
          price: R.compose(roundToKopeck, R.prop("price")),
          dropPrice: R.compose(roundToKopeck, R.prop("dropPrice")),
          sellingPrice: R.compose(roundToKopeck, R.prop("sellingPrice")),
          nomenclature: R.prop("nomenclature"),
        })
      ),
      R.propOr([], "products")
    ),
  ]),
  R.dissoc("productIds")
);

export const AddOrdersForm = ({ closeModal, refetch, batchOrderId }) => {
  const [addOrderToBatch] = useMutation(ADD_ORDER);

  const handleSubmit = async (values) => {
    try {
      await addOrderToBatch({
        variables: {
          batchOrderId,
          order: prepareValuesBeforeSubmit(values),
        },
      });
      closeModal();
      refetch();
    } catch (e) {
      alert(e);
    }
  };

  return <OrderForm onFinish={handleSubmit} />;
};
