import { gql, useQuery } from "@apollo/client";
import { Table } from "antd";
import * as R from "ramda";
import { useState } from "react";
import { Select } from "antd";
import moment from "moment";
import {
  calculateBatchOrderProfit,
  InfoWrapper,
} from "./batchOrder/BatchOrderTable";
import styled from "styled-components";
import { useModalContext } from "../providers/ModalProvider";
import { ProductsOrderTable } from "../components/ProductsOrderTable";
import { OrderDetailBtn } from "../components/Button";

const { Option } = Select;

const BATCH_ORDERS_QUERY_STAT = gql`
  query batchOrdersQuery($query: BatchOrdersQuery!) {
    batchOrders(query: $query) {
      _id
      dropBuyer {
        _id
        name
      }
      orders {
        id
        date
        totalPrice
        totalSellPrice
        totalDropPrice
        payed
        ttn
        buyerInfo
        status
        products {
          _id
          article
          count
          price
          dropPrice
          sellingPrice
          nomenclature
        }
      }
    }
  }
`;

const BATCH_PERIODS = gql`
  query {
    batchOrdersPeriods {
      periods
    }
  }
`;

const Center = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  font-weight: 700;
  font-size: 20px;
`;

const addDropBuyerToOrder = (batch) =>
  R.compose(
    R.map(
      R.converge(R.mergeRight, [
        R.identity,
        R.applySpec({ dropBuyerName: R.always(batch.dropBuyer.name) }),
      ])
    ),
    R.propOr([], "orders")
  )(batch);

const prepareOrders = R.compose(
  R.reverse,
  R.sortBy(R.prop("date")),
  R.unnest,
  R.map(addDropBuyerToOrder),
  R.propOr([], "batchOrders")
);

export const StatisticPage = () => {
  const [internalQuery, setInternalQuery] = useState(null);

  const { showModal } = useModalContext();

  const { data: periodsData } = useQuery(BATCH_PERIODS, {
    fetchPolicy: "network-only",
  });

  const { data, loading } = useQuery(BATCH_ORDERS_QUERY_STAT, {
    variables: {
      query: internalQuery,
    },
    skip: !internalQuery,
    fetchPolicy: "network-only",
  });

  const periods = R.pathOr([], ["batchOrdersPeriods", "periods"], periodsData);

  const handleChangeBatchFilter = (value) => {
    setInternalQuery({ name: value });
  };

  const orders = prepareOrders(data);

  const profit = calculateBatchOrderProfit(orders);

  const showOrderDetailModal = (products) =>
    showModal({
      title: "",
      width: 1000,
      content: () => <ProductsOrderTable products={products} type="drop" />,
    });

  const columnsConfig = [
    {
      title: "",
      dataIndex: "date",
      render: (value) => moment(value).format("YYYY-MM-DD"),
    },
    {
      title: "",
      dataIndex: "buyerInfo",
    },
    {
      title: "",
      dataIndex: "ttn",
    },
    {
      title: "",
      dataIndex: "dropBuyerName",
    },

    {
      title: "  ",
      dataIndex: "totalPrice",
    },
    {
      title: " ",
      dataIndex: "totalDropPrice",
    },

    {
      title: "",
      dataIndex: "payed",
    },

    {
      title: "",
      dataIndex: "status",
    },

    {
      dataIndex: "_id",
      render: (_, order) => (
        <OrderDetailBtn
          onClick={() => showOrderDetailModal(R.propOr([], "products", order))}
        />
      ),
    },
  ];

  return (
    <>
      <InfoWrapper>
        <Select
          style={{ width: 120, margin: "10px" }}
          onChange={handleChangeBatchFilter}
        >
          {periods.map((period) => (
            <Option key={period} value={period}>
              {period}
            </Option>
          ))}
        </Select>

        <Center>
          <>: {profit} </>
        </Center>
      </InfoWrapper>

      <Table
        columns={columnsConfig}
        dataSource={orders}
        loading={loading}
        rowKey="id"
        pagination={false}
        size="small"
      />
    </>
  );
};
