import { useQuery, gql } from "@apollo/client";
import { Table, Button } from "antd";
import * as R from "ramda";
import moment from "moment";

import { useModalContext } from "../../providers/ModalProvider";
import { useParams } from "react-router-dom";
import styled from "styled-components";
import { AddOrdersForm } from "./form/AddOrderForm";
import { UpdateOrdersForm } from "./form/UpdateOrderForm";
import {
  ButtonWrapper,
  EditButton,
  OrderDetailBtn,
} from "../../components/Button";
import { DeleteOrderBtn } from "./form/DeleteOrderBtn";
import { ORDER_STATUSES } from "./form/OrderForm";
import { roundToKopeck } from "../price/PriceTable/priceTableUtils";
import { ProductsOrderTable } from "../../components/ProductsOrderTable";

export const isInCalculateStatus = R.compose(
  R.includes(R.__, [ORDER_STATUSES.PAID, ORDER_STATUSES.PAID_BEFORE]),
  R.prop("status")
);

export const calculateBatchOrderTotal = R.curry((moved, orders) =>
  R.compose(
    roundToKopeck,
    R.converge(R.subtract, [
      R.compose(R.add(moved), R.sum, R.map(R.propOr(0, "payed"))),
      R.compose(R.sum, R.map(R.propOr(0, "totalDropPrice"))),
    ]),
    R.filter(isInCalculateStatus)
  )(orders)
);

export const calculateBatchOrderProfit = R.compose(
  roundToKopeck,
  R.converge(R.subtract, [
    R.compose(R.sum, R.map(R.propOr(0, "totalDropPrice"))),
    R.compose(R.sum, R.map(R.propOr(0, "totalPrice"))),
  ]),
  R.filter(isInCalculateStatus)
);

export const InfoWrapper = styled.div`
  display: flex;
  align-items: center;
  gap: 20px;
`;

export const BATCH_ORDER_QUERY = gql`
  query batchOrderQuery($id: ID!) {
    batchOrder(id: $id) {
      _id
      name
      dropBuyer {
        _id
        name
      }
      isActive
      movedTotal
      orders {
        id
        date
        totalPrice
        totalSellPrice
        totalDropPrice
        payed
        ttn
        buyerInfo
        status
        products {
          _id
          article
          count
          price
          dropPrice
          sellingPrice
          nomenclature
        }
      }
    }
  }
`;

export const BatchOrderTable = () => {
  const { batchOrderId } = useParams();
  const { showModal, handleCancel } = useModalContext();

  const { data, loading, refetch } = useQuery(BATCH_ORDER_QUERY, {
    variables: {
      id: batchOrderId,
    },
    skip: !batchOrderId,
    fetchPolicy: "network-only",
  });

  const batchOrderName = R.pathOr("", ["batchOrder", "name"], data);
  const dropBuyerName = R.pathOr("", ["batchOrder", "dropBuyer", "name"], data);
  const batchMovedTotal = R.pathOr("", ["batchOrder", "movedTotal"], data);

  const orders = R.compose(
    R.reverse,
    R.sortBy(R.prop("date")),
    R.pathOr([], ["batchOrder", "orders"])
  )(data);

  const total = calculateBatchOrderTotal(batchMovedTotal)(orders);

  const profit = calculateBatchOrderProfit(orders);

  const showAddOrderModal = () =>
    showModal({
      title: `    ${batchOrderName}`,
      content: () => (
        <AddOrdersForm
          closeModal={handleCancel}
          refetch={refetch}
          batchOrderId={batchOrderId}
        />
      ),
    });

  const showEditOrderModal = (order) =>
    showModal({
      title: ` `,
      content: () => (
        <UpdateOrdersForm
          closeModal={handleCancel}
          refetch={refetch}
          batchOrderId={batchOrderId}
          order={order}
        />
      ),
    });

  const showOrderDetailModal = (products) =>
    showModal({
      title: "",
      width: 1000,
      content: () => <ProductsOrderTable products={products} type="drop" />,
    });

  const columnsConfig = [
    {
      title: "",
      dataIndex: "date",
      render: (value) => moment(value).format("YYYY-MM-DD"),
    },
    {
      title: "",
      dataIndex: "buyerInfo",
    },
    {
      title: "",
      dataIndex: "ttn",
    },
    {
      title: "  ",
      dataIndex: "totalPrice",
    },
    {
      title: " ",
      dataIndex: "totalDropPrice",
    },

    {
      title: "",
      dataIndex: "payed",
    },

    {
      title: "",
      dataIndex: "status",
    },

    {
      dataIndex: "_id",
      render: (_, order) => (
        <ButtonWrapper>
          <OrderDetailBtn
            onClick={() =>
              showOrderDetailModal(R.propOr([], "products", order))
            }
          />
          <EditButton onClick={() => showEditOrderModal(order)} />
          <DeleteOrderBtn
            batchOrderId={batchOrderId}
            orderId={order.id}
            refetch={refetch}
          />
        </ButtonWrapper>
      ),
    },
  ];

  return (
    <>
      <InfoWrapper>
        <Button
          style={{ margin: "10px" }}
          type="primary"
          onClick={showAddOrderModal}
        >
           
        </Button>
        <p>: {batchOrderName}</p>
        <p>: {dropBuyerName}</p>
        <p>: {batchMovedTotal} </p>

        <h3>
          <b>: {total} </b>
        </h3>
        <p>: {profit} </p>
      </InfoWrapper>

      <Table
        columns={columnsConfig}
        dataSource={orders}
        loading={loading}
        rowKey="id"
        pagination={false}
        size="small"
        scroll={{ y: 800 }}
      />
    </>
  );
};
